:root{
    --square-side-length: 25px
}

.square {
    width: var(--square-side-length);
    height: var(--square-side-length);
    border: 1px solid black;
    color: black;
    text-align: center;
    font-size: medium;
}

.board {
  display: grid;
  grid-template-columns: repeat(7, var(--square-side-length));
  grid-template-rows: repeat(7, var(--square-side-length));
  gap: 0px 0px;
}
